function Nav(props){
    return(
        <li>
            <a href="#">{props.nav[props.i].one}</a>
            <div className="sub_back">
                <div className="sub_in">
                    <ul className="sub">
                        <li><a href="#">{props.nav[props.i].s1}</a></li>
                        <li><a href="#">{props.nav[props.i].s2}</a></li>
                        <li><a href="#">{props.nav[props.i].s3}</a></li>
                        <li><a href="#">{props.nav[props.i].s4}</a></li>
                    </ul>
                </div>
            </div>
        </li>
    )
}
export default Nav;