import { useState } from 'react';
import './App.css';
import List from './component/compo1';
import Util from './component/compo2';
import Lnav from './component/compo3';
import Nav from './component/compo4';
// import Main from './component/compo5';
import {Main, Slist, Section2, Slide3, Section4, Fdown} from './component/compo5';
// import nav from './data';
import {nav, m, sec1, sec3, sec4, fm, fimg} from './data';
// swiper
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import 'swiper/css/scrollbar';

// import required modules
import { Autoplay, Pagination, Navigation, A11y, Scrollbar  } from 'swiper/modules';

function App() {
  let [list,setList]=useState(false);
  return (
    <div className="App">
      <header id="header">
        <div className="up">
          <div className="left">
            <div className="language">
              <a href="#" onClick={()=>{
                setList(!list)
              }}>
                한국/한국어
                <span>
                  {
                    list==true ? <img src="/images/h_2.png" alt="arrow" /> : <img src="/images/h_1.png" alt="arrow" />
                  }
                  
                </span>
              </a>              
            </div>
            {
              list==true ? <List/> : null
            }
            
          </div>
          <h1 id="logo"><a href="#"></a></h1>
          <Util/>
        </div>
        <div className="down">
          <nav id="nav">
            <ul>
              {
                nav.map((a, i)=>{
                  return(
                    <Nav nav={nav} i={i}/>
                  )
                })
              }
              <li>
                <a href="">ABOUT 설화수</a>
                <Lnav/>
              </li>
            </ul>
          </nav>
        </div>
      </header>
      <div id="main">
        {/* <div> */}
        <Swiper
            spaceBetween={30}
            centeredSlides={true}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            pagination={{
              clickable: true,
            }}
            navigation={true}
            modules={[Autoplay, Pagination, Navigation]}
            className="mySwiper"
          >
          {
            m.map((a,i)=>{
              return(
                // <div>
                <SwiperSlide>
                  <Main m={m} i={i}/>
                  </SwiperSlide>
                // </div>
              )
            })
          }
          </Swiper>
        {/* </div> */}
      </div>
      <section id="section">
        <section id="section1">
          <h2>설화수추천</h2>
          <div className="boxWrap">
            <div className="slide">
              <div>
              <Swiper
                modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}
                spaceBetween={24}
                slidesPerView={3}
                slidesPerGroup={3}
                navigation
                loop={true}
                // pagination={{ clickable: true }}
                // scrollbar={{ draggable: true }}
                autoplay={true}
                onSwiper={(swiper) => console.log(swiper)}
                onSlideChange={() => console.log('slide change')}
              >
                {
                  sec1.map((a,i)=>{
                    return(
                      // <div>
                      <SwiperSlide>
                        <Slist sec1={sec1} i={i}/>
                      </SwiperSlide>
                      // </div>
                    )
                  })
                }
              </Swiper>  
              </div>
            </div>
          </div>
        </section>
        <Section2/>
        <section id="section3">
          <h2>설화수 시즌추천</h2>
          <div className="boxWrap">
            <div className="slide">
              <ul>
                {
                  sec3.map((a,i)=>{
                    return(
                      <Slide3 sec3={sec3} i={i}/>
                    )
                  })
                }
              </ul>
            </div>
          </div>
        </section>
        <Section4 sec4={sec4}/>
      </section>
      <footer id="footer">
        <div className="footIn">
          <div className="fmenu">
            <ul>
            {
              fm.map((a,i)=>{
                return(
                  <li><a href="#">{fm[i]}</a></li>
                )
              })
            }
            </ul>
            
          </div>
          <div className="fsns">
            <ul>
              {
                fimg.map((a)=>{
                  return(
                    <li><a href="#"><img src={`/images/sns${a}.png`} alt="1" /></a></li>
                  )
                })
              }
              
            </ul>
          </div>
        </div>
        <Fdown/>
      </footer>
    </div>
  );
}


export default App;
